          .col {
            height: 264px;
            position: relative; }
            .col:before, .col:after {
              content: '';
              display: block;
              width: 50%;
              height: 120%;
              background: linear-gradient(to right, white 15%, rgba(255, 255, 255, 0) 100%);
              position: absolute;
              top: 0;
              left: 0;
              z-index: 2; }
            .col:after {
              left: auto;
              right: 0;
              background: linear-gradient(to left, white 15%, rgba(255, 255, 255, 0) 100%); }
          
          .cards-slider {
            position: relative;
            max-width: 226px;
            margin: 0 auto; }
            .cards-slider:after {
              content: '';
              display: block;
              width: 100%;
              height: 264px;
              outline: 5px solid #61DAFB;
              position: absolute;
              top: 0;
              left: 0; }
